"use client";

import { useState, FormEvent } from 'react';
import { useRouter } from 'next/navigation';
import Link from 'next/link';
import { FiArrowLeft, FiCheck } from 'react-icons/fi';

interface ApartmentFeatures {
  price: number;
  size: number;
  rooms: number;
  bathroom: number;
  parking: number;
  furnished: number;
  elevator: number;
  balcony: number;
  floor: number;
  age: number;
  location_score: number;
}

interface PredictionResult {
  prediction: number;
  probability: { [key: string]: number };
}

// Default data for form
const defaultApartment: ApartmentFeatures = {
  price: 1000,
  size: 80,
  rooms: 2,
  bathroom: 1,
  parking: 1,
  furnished: 1,
  elevator: 1,
  balcony: 1,
  floor: 2,
  age: 5,
  location_score: 7
};

// Apartment category labels
const categoryLabels = ['Budget', 'Standard', 'Premium'];

export default function PredictPage() {
  const router = useRouter();
  const [apartment, setApartment] = useState<ApartmentFeatures>(defaultApartment);
  const [loading, setLoading] = useState<boolean>(false);
  const [modelType, setModelType] = useState<string>("random_forest");
  const [result, setResult] = useState<PredictionResult | null>(null);
  const [error, setError] = useState<string | null>(null);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value, type } = e.target as HTMLInputElement;
    const parsedValue = type === 'number' ? parseFloat(value) : value;
    
    setApartment((prev) => ({
      ...prev,
      [name]: parsedValue
    }));
  };

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      const response = await fetch('http://localhost:8080/predict/?model_name=' + modelType, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(apartment),
      });

      if (!response.ok) {
        throw new Error('Failed to get prediction');
      }

      const data = await response.json();
      setResult(data);
    } catch (err) {
      setError('Error making prediction. Please make sure the backend server is running.');
      console.error(err);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-gray-50 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-4xl mx-auto">
        <div className="flex items-center mb-8">
          <Link href="/" className="flex items-center text-blue-600 hover:text-blue-800">
            <FiArrowLeft className="mr-2" /> Back to Dashboard
          </Link>
        </div>
        
        <div className="bg-white shadow-md rounded-lg overflow-hidden">
          <div className="px-6 py-4 bg-blue-700 text-white">
            <h1 className="text-xl font-semibold">Apartment Rental Prediction</h1>
          </div>

          <div className="p-6">
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="bg-blue-50 p-4 rounded-md mb-6">
                <h2 className="text-lg font-medium text-blue-800 mb-2">Select Model</h2>
                <div className="flex space-x-4">
                  <label className="inline-flex items-center">
                    <input
                      type="radio"
                      name="model"
                      value="random_forest"
                      checked={modelType === "random_forest"}
                      onChange={() => setModelType("random_forest")}
                      className="form-radio h-5 w-5 text-blue-600"
                    />
                    <span className="ml-2">Random Forest</span>
                  </label>
                  
                  <label className="inline-flex items-center">
                    <input
                      type="radio"
                      name="model"
                      value="knn"
                      checked={modelType === "knn"}
                      onChange={() => setModelType("knn")}
                      className="form-radio h-5 w-5 text-blue-600"
                    />
                    <span className="ml-2">KNN</span>
                  </label>
                  
                  <label className="inline-flex items-center">
                    <input
                      type="radio"
                      name="model"
                      value="naive_bayes"
                      checked={modelType === "naive_bayes"}
                      onChange={() => setModelType("naive_bayes")}
                      className="form-radio h-5 w-5 text-blue-600"
                    />
                    <span className="ml-2">Naive Bayes</span>
                  </label>
                </div>
              </div>

              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label htmlFor="price" className="block text-sm font-medium text-gray-700">
                    Price ($)
                  </label>
                  <input
                    type="number"
                    name="price"
                    id="price"
                    value={apartment.price}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    min="0"
                    step="100"
                    required
                  />
                </div>

                <div>
                  <label htmlFor="size" className="block text-sm font-medium text-gray-700">
                    Size (mÂ²)
                  </label>
                  <input
                    type="number"
                    name="size"
                    id="size"
                    value={apartment.size}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    min="10"
                    step="5"
                    required
                  />
                </div>

                <div>
                  <label htmlFor="rooms" className="block text-sm font-medium text-gray-700">
                    Rooms
                  </label>
                  <input
                    type="number"
                    name="rooms"
                    id="rooms"
                    value={apartment.rooms}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    min="1"
                    max="10"
                    step="1"
                    required
                  />
                </div>

                <div>
                  <label htmlFor="bathroom" className="block text-sm font-medium text-gray-700">
                    Bathrooms
                  </label>
                  <input
                    type="number"
                    name="bathroom"
                    id="bathroom"
                    value={apartment.bathroom}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    min="0"
                    max="5"
                    step="1"
                    required
                  />
                </div>

                <div>
                  <label htmlFor="parking" className="block text-sm font-medium text-gray-700">
                    Parking Available
                  </label>
                  <select
                    name="parking"
                    id="parking"
                    value={apartment.parking}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    required
                  >
                    <option value={1}>Yes</option>
                    <option value={0}>No</option>
                  </select>
                </div>

                <div>
                  <label htmlFor="furnished" className="block text-sm font-medium text-gray-700">
                    Furnished
                  </label>
                  <select
                    name="furnished"
                    id="furnished"
                    value={apartment.furnished}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    required
                  >
                    <option value={1}>Yes</option>
                    <option value={0}>No</option>
                  </select>
                </div>

                <div>
                  <label htmlFor="elevator" className="block text-sm font-medium text-gray-700">
                    Elevator
                  </label>
                  <select
                    name="elevator"
                    id="elevator"
                    value={apartment.elevator}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    required
                  >
                    <option value={1}>Yes</option>
                    <option value={0}>No</option>
                  </select>
                </div>

                <div>
                  <label htmlFor="balcony" className="block text-sm font-medium text-gray-700">
                    Balcony
                  </label>
                  <select
                    name="balcony"
                    id="balcony"
                    value={apartment.balcony}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    required
                  >
                    <option value={1}>Yes</option>
                    <option value={0}>No</option>
                  </select>
                </div>

                <div>
                  <label htmlFor="floor" className="block text-sm font-medium text-gray-700">
                    Floor
                  </label>
                  <input
                    type="number"
                    name="floor"
                    id="floor"
                    value={apartment.floor}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    min="0"
                    max="50"
                    step="1"
                    required
                  />
                </div>

                <div>
                  <label htmlFor="age" className="block text-sm font-medium text-gray-700">
                    Building Age (years)
                  </label>
                  <input
                    type="number"
                    name="age"
                    id="age"
                    value={apartment.age}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    min="0"
                    max="100"
                    step="1"
                    required
                  />
                </div>

                <div>
                  <label htmlFor="location_score" className="block text-sm font-medium text-gray-700">
                    Location Score (1-10)
                  </label>
                  <input
                    type="number"
                    name="location_score"
                    id="location_score"
                    value={apartment.location_score}
                    onChange={handleChange}
                    className="mt-1 block w-full border border-gray-300 rounded-md shadow-sm py-2 px-3 focus:outline-none focus:ring-blue-500 focus:border-blue-500"
                    min="1"
                    max="10"
                    step="1"
                    required
                  />
                </div>
              </div>

              <div className="mt-6">
                <button
                  type="submit"
                  disabled={loading}
                  className="w-full inline-flex justify-center py-3 px-4 border border-transparent shadow-sm text-base font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500"
                >
                  {loading ? 'Processing...' : 'Get Prediction'}
                </button>
              </div>
            </form>
          </div>

          {error && (
            <div className="p-6 bg-red-50 border-t border-red-200">
              <div className="text-red-700">
                <p>{error}</p>
              </div>
            </div>
          )}

          {result && !error && (
            <div className="p-6 bg-green-50 border-t border-green-200">
              <div className="flex items-center mb-4">
                <div className="bg-green-100 p-2 rounded-full">
                  <FiCheck className="text-green-600 text-xl" />
                </div>
                <h2 className="ml-3 text-xl font-medium text-green-800">Prediction Result</h2>
              </div>

              <div className="mb-4">
                <p className="text-gray-600">Apartment Category:</p>
                <p className="text-2xl font-semibold text-gray-900">
                  {categoryLabels[result.prediction]}
                </p>
              </div>

              <div>
                <p className="text-gray-600 mb-2">Probability Distribution:</p>
                <div className="space-y-2">
                  {Object.entries(result.probability).map(([category, prob]) => (
                    <div key={category} className="flex items-center">
                      <span className="w-24">{categoryLabels[parseInt(category)]}:</span>
                      <div className="flex-1 bg-gray-200 rounded-full h-4">
                        <div
                          className="bg-blue-600 h-4 rounded-full"
                          style={{ width: `${prob * 100}%` }}
                        ></div>
                      </div>
                      <span className="ml-2">{(prob * 100).toFixed(1)}%</span>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}